import React from "react";
import { Container, Row, Col, Card, CardGroup } from "react-bootstrap";
import "./recent.css";
import sql from "../../../assets/images/sql.png";
import end2end from "../../../assets/images/end2end.png";
import visual from "../../../assets/images/visual.png";
import python from "../../../assets/images/python.png";

export default function RecentWorks() {
	const recentWorks = [
		{
			title: "End-to-End Project",
			subTitile: "Tech stack: Python, SQL, AWS, GCP",
			discription:
				"Building end-to-end pipeline on cloud services to streamline data into creating meaningful insight",
			image: end2end,
			link: "/myprj",
			target: "",
		},
		{
			title: "SQL Challenges",
			subTitile:
				"Topic includes (CTE. Advance Window Function, Case When/ IIF, Gaps and Island problems, ...)",
			discription:
				"Hands-on practice with advanced SQL constructs to solve business reporting and analytics challenges efficiently",
			image: sql,
			link: "/myprj",
			target: "",
		},
		{
			title: "Python Project",
			subTitile:
				"Topic include EDA, Regression, T-test, A/B Test, Chi-squared, machine learning model,..)",
			discription: "Statistical Analysis in Python (regression, Chi-Square)",
			image: python,
			link: "https://github.com/MarkPhamm/Python-Segment-Shopping-Customers/tree/main",
			target: "_blank",
		},
		{
			title: "Visualization",
			subTitile:
				"Topics include chart selection, color usage, metrics identification",
			discription:
				"Data Visualization via Tableau and identified key metrics to discover critical insights and trends",
			image: visual,
			link: "https://public.tableau.com/app/profile/minh.pham1154/viz/TableauProject3_16459122653330/SampleSalessDashboard",
			target: "_blank",
		},
	];

	return (
		<div className="half-white-half-dark">
			<Container className="pb-5">
				<Row className="text-center text-white py-5">
					<Col>
						<h2 className="fw-bolder mb-0" style={{ color: "#ff9900" }}>
							Recent Works
						</h2>
					</Col>
				</Row>
				<Col md={6} xs={12} className="offset-md-3 mb-5 mb-lg-0 text-center">
					<Row xs={1} md={2} className="g-1">
						{recentWorks.map((item, index) => (
							<Col key={index}>
								<Card className="custom-card">
									<Card.Img
										className="rounded-0 border-0 card-image"
										variant="top"
										src={item.image}
										style={{ objectFit: "cover" }}
									/>
									<Card.Body className="hover-info bottom-0">
										<Card.Title className="cus-card-title">
											{item.title}
										</Card.Title>
										<Card.Text className="cus-card-subtitle">
											{item.subTitile}
										</Card.Text>
										<Card.Text className="cus-card-description">
											{item.discription}
										</Card.Text>
										<Card.Link
											href={item.link}
											className="btn btn-warning text-black"
											target={item.target}
										>
											View more
										</Card.Link>
									</Card.Body>
								</Card>
							</Col>
						))}
					</Row>
				</Col>
			</Container>
		</div>
	);
}
