import React from "react";
import { Container, Row, Col, Card } from "react-bootstrap";
import featureImg from "../../../assets/images/Cloud 5.png";
import "./index.css";

export default function FeatureProject() {
	return (
		<section id="begin">
			<Container fluid className="feature-project-container">
				<Row>
					<h2 className="text-center p-2">Featured Project</h2>
				</Row>
				<Row className="d-flex justify-content-center">
					<Col md={6}>
						<Card className="custom-card">
							<Card.Img
								className="rounded-0 border-0 card-image"
								variant="top"
								src={featureImg}
							/>
							<Card.Body className="hover-info bottom-0">
								<Card.Title className="cus-card-title">
									Spotify End-to-End Business Intelligence Engineer Project
								</Card.Title>
								<Card.Text className="cus-card-subtitle">
									Tech Stack: Python, SQL, AWS S3, AWS Athena, Aws Glue, AWS
									Redshift, AWS Lambda
								</Card.Text>
								<Card.Text className="cus-card-description">
									In this project, we will build an automated ETL pipeline to
									extract Spotify data via the Spotify API, orchestrate a daily
									triggered system with CloudWatch/Lambda functions, and
									construct an interactive dashboard with Amazon Quicksight
								</Card.Text>
								<Card.Link href="#" className="btn btn-warning text-black">
									View more
								</Card.Link>
							</Card.Body>
						</Card>
					</Col>
				</Row>
			</Container>
		</section>
	);
}
